:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #ffffff;
  --color-light: rgba(255, 255, 255, 0.6);

  --container-with-lg: 75%;
  --container-with-md: 86%;
  --container-with-sm: 90%;

  --trans-ease: all 0.4s ease;

}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  }
  
  body {
    font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1f1f38;
  background: var(--color-bg);
    color: #ffffff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
    width: 100vw;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  ::\--webkit-scrollbar {
    display: none;
  }
  
  li {
    list-style-type: none;
    padding: 0;
  }
  
  button {
    cursor: pointer;
  }
  
  a {
    color: #4db5ff;
    color: var(--color-primary);
    text-decoration: none;
    transition: all 0.4s ease;
    transition: var(--trans-ease);
  }
  
  a:hover {
    color: #ffffff;
    color: var(--color-white);
  }
  
  .container {
    width: 75%;
    width: var(--container-with-lg);
    margin: 0 auto;
  
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 500;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  section {
    margin-top: 8rem;
    height: 100vh;
  }
  
  section>h2,
  section>h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
  }
  
  section>h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
  }
  
  .text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
  }
  
  .btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.3rem;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 0.4s ease;
    transition: var(--trans-ease);
  }
  
  .btn:hover {
    background: #ffffff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
  }
  
  .btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
  }
  
  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }
  
  
  /* RESPONSIVE SETTINGS */
  
  /* Tablets */
  @media screen and (max-width: 1024px) {
    .container {
      width: 86%;
      width: var(--container-with-md);
    }
  
    section {
      margin-top: 6rem;
    }
  }
  
  /* Mobiles */
  @media screen and (max-width: 600px) {
    .container {
      width: 90%;
      width: var(--container-with-sm);
    }
  
    section>h2 {
      margin-bottom: 2rem;
    }
}
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

header .header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* -------CTA------- */

.cta {
    margin: 2.5rem 0;
    display: flex;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;

}

/* SOCIAL LINKS */

.social-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 5rem;
    bottom: 10rem;
}

.social-links::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.social-links a {
    font-size: 1.5rem;
}


header .ahmet img {
    margin: 0 auto;
    width: 300px;
    height: 300px;
    border-radius: 12rem 12rem 0 0;
    padding: 5px;
    border: 2px solid var(--color-primary);
    transition: var(--trans-ease);
    cursor: pointer;

}

header .ahmet img:hover {
    padding: 0;
    -webkit-transform: rotate(0);
            transform: rotate(0)
}

header .scroll__down {
    position: absolute;
    right: 5rem;
    bottom: 10rem;
    font-size: 1.5rem;
}

/* RESPONSIVE SETTINGS */

/* Tablets */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

}

/* Mobiles */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .social-links,
    .scroll__down {
        display: none;
    }
}
nav {
    display: block;
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    padding: 0.7rem 1.7rem;
    position: fixed;
    z-index: 2;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 3rem;
    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    border-radius: 1rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    display: flex;
    align-items: center;
    grid-gap: 0.3rem;
    gap: 0.3rem;
}

nav a.active {
    color: var(--color-white);
    font-weight: bold;
    border-bottom: 1px solid var(--color-white);
}
/* Mobiles */
@media screen and (max-width: 600px) {
    nav span {
        display: none;
    }
}
#about h2 {
    margin-bottom: 6rem;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;

}

.about__me {
    width: 100%;
    height: 80%;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary));
    display: grid;
    place-items: center;
}

.about__me-image {
    width: 100%;
    height: 100%;
    transition: var(--trans-ease);
    border-radius: 2rem;
    overflow: hidden;
    cursor: pointer;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
}

.about__me-image:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
}

/* Cards */

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    text-align: center;
    padding: 2rem;
    transition: var(--trans-ease);
    cursor: pointer;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary);
}

.about__card .about_icon {
    color: var(--color-primary);
    margin-bottom: 1rem;
    font-size: 1.4rem;
}

.about__card h5 {
    font-size: 1rem;
    letter-spacing: 1px;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light)
}

.about__content p {
    margin: 2rem 0rem 2.6rem;
    color: var(--color-light)
}
/* RESPONSIVE SETTINGS */

/* Tablets */
@media screen and (max-width: 1024px) {
    #about h2 {
        margin-bottom: 0.9rem;
    }

    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0rem 1.6rem;
    }
}

/* Mobiles */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0rem auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }
}
#skills {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#skills>h3 {
    text-align: center;
    margin: 1.5rem 0;
}

.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container>div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--trans-ease)
}

.experience__container>div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container>div h3 {
    text-align: center;
    margin-bottom: 2rem;
    letter-spacing: 1px;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;

    grid-gap: 1rem;

    gap: 1rem
}

.experience__details-icons {
    margin-top: 6px;
    color: var(--color-primary);
}

/* RESPONSIVE SETTINGS */

/* Tablets */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container>div {
        width: 90%;
        margin: 0 auto;
        padding: 2rem;
    }

    .experience__content {
        padding: 1rem;
    }
}

/* Mobiles */
@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container>div {
        width: 100%;
        padding: 2rem 1rem;
    }
}
#portfolio {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#portfolio>h4 {
    text-align: center;
}

.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio_item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--trans-ease);

}

.portfolio_item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio_item-image {
    border-radius: 1rem;
    overflow: hidden;
}

.portfolio_item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}
.portfolio__item-cta .btn {
    padding: 0.15rem .85rem;
}

/* RESPONSIVE SETTINGS */

/* Tablets */
@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* Mobiles */
@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
        gap: 1rem;
    }
}
#testimonials {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

#testimonials>h4 {
    text-align: center;
}

.testimonials__container {
    width: 40%;
    margin: 0 auto;
    
    }
    
    .testimonial {
        position: relative;
        background: var(--color-bg-variant);
        text-align: center;
        padding: 2rem;
        border-radius: 2rem;
        -webkit-user-select: none;
                user-select: none;
        transition: var(--trans-ease)
    }
    
    .left,
    .right {
        background: transparent;
        border: none;
        font-size: 2rem;
        color: var(--color-primary-variant);
        position: absolute;
        top: 50%;
        z-index: 10;
    }
    
    .left:disabled,
    .right:disabled {
        cursor: not-allowed;
        color: rgba(0, 0, 0, 0.5)
    }
    
    .left {
        left: 1rem;
    
    }
    
    .right {
        right: 1rem;

}

.client__avatar {
    width: 6rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 1rem;
    border: .4rem solid var(--color-primary-variant);
}
.client__name {
    text-transform: uppercase;
    font-weight: bold;
}

.client__prof {
    color: var(--color-primary-variant);
}

.client__review {
    color: var(--color-light);
    font-weight: 200;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

.qu__left {
    margin-right: 0.5rem;
    color: white
}

.qu__right {
    margin-left: 0.5rem;
    color: white
}

/* RESPONSIVE SETTINGS */

/* Tablets */
@media screen and (max-width: 1024px) {
    .testimonials__container {
        width: 60%;
    }
}

/* Mobiles */
@media screen and (max-width: 600px) {
    .testimonials__container {
        width: var(--container-width-sm);
    }

    .client__review {
        width: var(--container-width-sm);
    }
}
#contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#contact>h4 {
    text-align: center;
}

.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
    position: relative;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    transition: var(--box-shadow);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option h5 {
    color: var(--color-light)
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

.scroll__up {
    position: absolute;
    right: -5rem;
    bottom: 0rem;
    font-size: 1.5rem;
}


/* ----------Form---------- */

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.6rem;
    gap: 1.6rem
}

form button.btn-anim {
    width: 150px;
    height: 50px;
}
input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: white;
    font-weight: bold;
}

/* Tablets */
@media screen and (max-width: 1024px) {
    .contact__container {
        width: 80%;
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* Mobiles */
@media screen and (max-width: 600px) {

.contact__container {
        width: 90%
    }

    .scroll__up {
        display: none;
    }
}
.loading {
    display: inline-block;
    width: 30px;
    height: 30px;

    border-radius: 50%;
    border: 2px solid gray;
    border-bottom-color: teal;
    border-left-color: teal;
    border-right-color: teal;
    position: relative;
    -webkit-animation: turner 1s infinite linear;
            animation: turner 1s infinite linear;
}

@-webkit-keyframes turner {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes turner {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}
footer {
    background: var(--color-primary);
    padding: 2rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 5rem;
}

footer a {
    color: var(--color-bg)
}

.footer__logo {
    display: inline-block;
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
    letter-spacing: 2px;
}

.footer__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
    font-size: 1.3rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    transition: var(--trans-ease)
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* Mobiles */
@media screen and (max-width: 600px) {
    .footer__links {
        flex-direction: column;
        grid-gap: 1.2rem;
        gap: 1.2rem
    }

    .footer__socials {
        margin-bottom: 1.6rem;
        font-size: 1rem;
    }
}
